*{
    margin:0;
    padding:0;
    font-family: 'Poppins', sans-serif;
    font-size: medium;
    
}
.image{
    width: 100%;
    height: 100vh;
    background-image:  url(https://wallpaperaccess.com/full/1431622.jpg);
    background-size: cover;
    background-position: center;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}   
.log1{
 display: flex;
 justify-content: center;
 padding-top: 10px;
 font-size: 25px;
 font-family: monospace;
}
.container{
    display:flex;
    justify-content: center;
    align-items: center;
    padding-top: 70px;
}


.container .drop
{
    position: relative;
    width: 430px;
    height: 430px;
    box-shadow: inset 20px 20px 20px rgba(0,0,0,0.1),
    25px 35px 20px rgba(0,0,0,0.05),
    25px 35px 30px rgba(0,0,0,0.05),
    inset -20px -20px 25px rgba(255,255,255,0.9);
    transition:0.5s;
    display: flex;
    justify-content: center;
    align-items: center;
    justify-items: center;
    position: relative;
   
    border-radius:41% 59% 34% 66% / 46% 31% 69% 54% 
}

.container .drop:hover{
    border-radius: 50%;
}
