*{
    margin:0;
    padding:0;
    font-family: 'Poppins', sans-serif;
    font-size: medium;
    
}
.image012{
    width: 100%;
    height: 100vh;
    background-image:  url(https://www.nerdwallet.com/assets/blog/wp-content/uploads/2021/07/acft-a350-4-2400x1440.jpeg);
    background-size: cover;
    background-position: center;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-attachment: fixed;    
    
}   



.container012 .drop012
{
    position: relative;
    width: 430px;
    height: 430px;
    box-shadow: inset 20px 20px 20px rgba(0,0,0,0.1),
    25px 35px 20px rgba(0,0,0,0.05),
    25px 35px 30px rgba(0,0,0,0.05),
    inset -20px -20px 25px rgba(255,255,255,0.9);
    transition:0.5s;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius:41% 59% 34% 66% / 46% 31% 69% 54% 
}

.container012 .drop012:hover{
    border-radius: 50%;
}




