.hero{
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-color: rgb(255, 255, 255);
  
}


 
.intro{
  
  

  position: absolute;
  
  top: 25%;
  
  left: 40%;
  
  transform: translate(-50%, -50%);
  
  font-size: 70px;
  
  -webkit-text-stroke: 1px rgb(255, 255, 255);
  
  color: transparent;
  
  background-image: linear-gradient(45deg,rgb(0, 0, 0), rgb(0, 0, 0));
  background-repeat: no-repeat;
  
  -webkit-background-clip: text;
  
  background-position: -330px 0; 
  animation: backcolor 7s linear infinite alternate;
  
  } @keyframes backcolor {
  
  100%{
  
  background-position: 0 0;
  
  
  
  }


}
.trip{
        position: absolute;
        top: 38%;
        left:50%;
        color:rgb(0, 0, 0);
        -webkit-text-stroke: .2px #fff;
        font-style: italic;
        font-size: 30px;
       
      }
      
      .vis
      {
         position: absolute;
        top: 88%;
        left:50%;
        font-size: 0.9em;
        font-weight: 100;
        letter-spacing: 1px;
        color: #fff;
        /* -webkit-text-stroke: 1px rgb(231, 225, 225); */
        
      }
      .vis:hover{
        text-decoration-line: underline ;
        
      }
      video{
        width:100%;
        height: 100%;
        object-fit: cover;
      }
.overlay{
  position: absolute;
  height: 100%;
  width:100%;
  background-color: rgb(0, 0, 0,);
}


